@media all {
    * {
        box-sizing: border-box;
    }

    h1 {
        color: #96db37;
    }

    .list-headline h2 {
        color: #666;
        font-size: 14px;
    }

    .list-links {
        list-style: none;
        padding: 0 0 0 0;

    }

    .list-links li {
        margin-bottom: 20px;
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
    }

    .list-links li div {

    }

    .views-field-field-url {
        flex: 0 0 220px;
    }
    .views-field-field-description {
        min-width: 280px;
        flex: 1;
    }


    #content * a {
        color: #96db37;
        text-decoration: none;
    }
    #content * a:hover {
        text-decoration: underline;
    }

    .blog-entry-readmore a {
        color: #999 !important;
    }
    .blog-entry-readmore a:hover {
        text-decoration: none !important;
    }

    .links-container * h2 {
        padding-top: 20px;
    }

    body {
        background-color: #ffffff;
        font-family: 'Open Sans', Arial, Helvetica, Verdana, sans-serif;
        font-size: 14px;
        min-width: 360px;
        min-height: 100%;

        padding: 0;
        margin: 0;
    }

    a:hover {
        color: #96db37;
        text-decoration: none;
    }

    header, #toolbar-top {
        width: 100%;
        display: flex;
        flex-direction: row;


        background-color: #FFFFFF;
        box-sizing: border-box;
    }

    header {
        margin: 0;
        padding: 0;
        padding-left: 20px;
        box-shadow:0px 2px -4px #494a48;
        flex-wrap: wrap;
    }

    #toolbar-top {
        padding-top: 10px;
        padding-right: 20px;
        flex-wrap: wrap-reverse;
    }

    #mobile-menu
    {
        display: none;
    }

    #social {
        flex-grow: 1;
        padding-right: 40px;
        text-align: right;
        font-size: 20px;
    }

    #social a {
        color: #999;
        margin-right: 15px;
    }

    #search {
        display: flex;
        align-self: flex-end;
        margin-left: auto;
        margin-bottom: 0;
    }

    #search form {
        padding: 0;
        margin: 0;
        display: block;
        box-sizing: border-box;
    }

    #search input {
        display: inline-block;
        border: none;
        height: 25px;
        padding-left: 10px;
        color: #666;
        border: 1px solid #999;
        line-height: normal;
        box-sizing: border-box;
    }

    #search input[type="search"] {
        background-color: #ffffff;
    }

    #search input[type="submit"] {
        width: 30px;

        background-color: #999;
        color: #ffffff;

        font-size: 13px;

        -webkit-appearance: button;
        cursor: pointer;
    }

    #header-logo {
        padding-top: 10px;
        padding-bottom: 5px;
        max-width: 400px;
    }

    #header-logo img {
        max-width: 100%;
    }

    menu {
        display: flex;
        align-self: flex-end;
        margin-left: auto;
        margin-bottom: 0;
        margin-top: 0;
    }

    nav ul {
        text-transform: uppercase;
        font-weight: bold;
        font-size: 14px;
        list-style-type: disc;
        margin-bottom: 10px;
    }

    nav ul li {
        display: inline;
        flex-grow: 1;
        margin-right: 20px;
    }

    nav ul li a {
        color: #666;
        text-decoration: none;
    }

    nav ul li a:hover {
        color: #96db37;
        text-decoration: none;
    }

    nav ul li a.is-active,
    nav ul .menu-item--active-trail a {
        border-bottom: solid 1px #666;
        padding-bottom: 0.4px;
        border-top: solid 1px #666;
        padding-top: 0.4px;
    }

    main
    {
        background-color: #ffffff;
        display: flex;
        flex-wrap: wrap;
        padding: 20px;
        width: 100%;
        box-sizing: border-box;
    }

    #content
    {
        flex: 100;
        padding-right: 30px;
        margin-bottom: 30px;
        margin: 0 auto;
    }

    #infobox
    {
        flex: 1;
        min-width: 260px;
    }

    #infobox * h2
    {
        border-left: 1px dotted #96db37;
        padding-left: 10px;
        padding-top: 5px;
        padding-bottom: 5px;
        font-family: 'Open Sans','Lucida Sans Unicode','Lucida Grande',sans-serif;
        font-size: 18px;
        color: #666;
        font-style: normal;
        font-weight: 300;

        letter-spacing: -1px;
        text-rendering: optimizeLegibility;
        -webkit-font-smoothing: antialiased;
        margin-bottom: 10px;
        margin-top: 0;
    }

    #infobox * h3
    {
        color: #999;
        padding: 0;
        margin: 0;
        font-size: 14px;
    }

    #infobox * h3 a
    {
        padding-right: 5px;
        color: #999;
        font-size: 14px;

        text-decoration: none;
        font-weight: 300;
    }

    #infobox * h3 a:hover {
        color: #96db37;
        text-decoration: none;
    }

    .block-blog-categories-row,
    .block-blog-newwest-entries-row,
    .block-blog-archive-row
    {
        margin-left: 11px;
    }

    .views-field-field-category-image * img
    {
        max-width: 100%;
        height: auto;
    }

    .block-blog-newwest-entries-row
    {
        padding-bottom: 5px;
        margin-bottom: 5px;
        border-bottom: 1px solid #ccc;
    }

    .block-blog-newest-entries-date,
    .blog-entry-date,
    .blog-entry-container * .views-field-field-blog-category,
    .blog-container * .views-field-field-blog-category,
    .content-container * .views-field-field-blog-category
    {
        color: #ccc;
        font-weight: bold;
        font-size: 11px;
        margin-bottom: 20px;
    }

    .content-container * .views-field-field-blog-category,
    .content-container * .views-field-created,
    .blog-container * .views-field-field-blog-category,
    .blog-container * .views-field-created,
    .blog-entry-container * .views-field-field-blog-category,
    .blog-entry-container * .views-field-created {
        display: inline-block;
    }

    .content-container * .views-field-field-blog-category,
    .blog-entry-container * .views-field-field-blog-category,
    .blog-container * .views-field-field-blog-category {
        padding-left: 30px;
        font-weight: bold;
    }

    .content-container * .views-field-field-blog-category a,
    .blog-entry-container * .views-field-field-blog-category a,
    .blog-container * .views-field-field-blog-category a {
        color: #96db37;
        font-weight: normal;
        text-decoration: none;
    }

    .content-container * .views-field-field-blog-category a:hover,
    .blog-container * .views-field-field-blog-category a:hover,
    .blog-entry-container * .views-field-field-blog-category a:hover {
        text-decoration: underline;

    }

    .date-icon
    {
        font-size: 11px !important;
    }

    #infobox * .views-element-container
    {
        margin-bottom: 20px;
    }

    .spacer
    {
        height: 40px;
        width: 100%;
        display: inline-block;
        background-image: url(../images/spacer.png)
    }

    footer
    {
        background-color: #494a48;
        width: 100%;
        display: flex;
        color: #fff;
        box-sizing: border-box;
        flex-wrap: wrap;
    }

    footer img
    {
        display: block;
        margin: 10px auto 10px auto;
    }

    #footer-left ul
    {
        list-style-type: none;
        color: #909090;
        padding: 0;
    }

    #footer-left ul li
    {
        padding: 10px 0 10px 30px;
    }

    .footer-imprint-location
    {
        background: url('../images/icons/address_icon.png') no-repeat left center transparent;
    }

    .footer-imprint-mobile
    {
        background: url('../images/icons/phone_icon.png') no-repeat left center transparent;
    }

    .footer-imprint-mail
    {
        background: url('../images/icons/email_icon.png') no-repeat left center transparent;
    }
    .footer-imprint-name
    {
        background: url('../images/icons/name_icon.png') no-repeat left center transparent;
    }

    #footer-left,
    #footer-right
    {
        font-size: 12px;
        color: #ccc;
        padding: 10px 20px 10px 20px;
        flex: 1;
    }

    #footer-left p
    {
        font-family: 'Open Sans','Lucida Sans Unicode','Lucida Grande',sans-serif;
    }

    .footer-dark
    {
        font-size: 11px;
        color: #909090;
    }

    .footer-headline
    {
        font-weight: 600;
        font-size: 14px;
        color: #f2f2f2;
    }

    #footer-bottom
    {
        padding: 20px;
        width: 100%;
        border-top: 1px solid #666;
        font-family: 'Open Sans','Lucida Sans Unicode','Lucida Grande',sans-serif;
        font-weight: normal;
        color: #909090;
        font-size: 12px;
    }


    .content-container,
    .blog-container,
    .blog-entry-container
    {
        width: 100%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        padding: 0;
    }

    .links-container
    {
        width: 100%;
        display: block;
        padding: 0;
    }

    .views-field-field-blog-subtitle {
        margin-top: 10px;
        color: #666;
        font-weight: bold;
    }

    .links-container > .views-row,
    .content-container > .views-row,
    .blog-container > .views-row
    {
        padding: 0;
        padding-right: 10px;
        width: 400px;
    }

    .blog-entry-container > .views-row
    {
        padding: 0;
        padding-right: 10px;
        width: 100%;
    }

    #content * h1
    {
        padding: 0;
        margin: 0;
    }

    #blog-entry-title
    {
        display: block;
    }

    .node-blog-entry-title {
        display: block !important;
    }

    .content-container > .views-row * h2,
    .blog-container > .views-row * h2,
    .blog-entry-container > .views-row * h2
    {
        height: 30px;
        padding: 0;
        padding-top: 5px;
        padding-bottom: 5px;
        font-family: 'Open Sans','Lucida Sans Unicode','Lucida Grande',sans-serif;
        font-size: 18px;
        font-style: normal;
        font-weight: bold;
        letter-spacing: -1px;
        text-rendering: optimizeLegibility;
        -webkit-font-smoothing: antialiased;
        margin-bottom: 10px;
        margin-top: 0;
        box-sizing: content-box;
    }

    .node-blog-entry-title {
        height: auto;
        padding-top: 5px;
        padding-bottom: 0px;
        font-family: 'Open Sans','Lucida Sans Unicode','Lucida Grande',sans-serif;
        font-size: 18px;
        font-style: normal;
        font-weight: bold;
        letter-spacing: -1px;
        text-rendering: optimizeLegibility;
        -webkit-font-smoothing: antialiased;
        margin-bottom: 0px;
        margin-top: 0;
        box-sizing: content-box;
    }

    .content-container > .views-row * h2 a,
    .blog-container > .views-row * h2 a,
    .blog-entry-container > .views-row * h2 a,
    .node-blog-entry-title
    {
        color: #96db37;
        text-decoration: none;
    }

    .content-container > .views-row * h2 a:Hover,
    .blog-container > .views-row * h2 a:Hover
    .blog-entry-container > .views-row * h2 a:Hover
    {
        color: #666;
        text-decoration: underline;
    }

    nav.pager
    {
        padding: 20px;
        width: 100%;
        text-align: center;
    }

    .pager ul .is-active a
    {
        text-decoration: underline;
    }

    .blog-container * .views-field-body
    {
        padding-right: 30px;
    }

    .content-container * .views-field-body,
    .blog-container * .views-field-body,
    .blog-entry-container * .views-field-body,
    .content-container * .views-field-field-description
    {
        color: #666;
        font-size: 14px;
        text-align: justify;
    }

    .views-field-field-teaser-image
    {
        max-width: 320px;
    }

    .blog-entry-readmore
    {
        margin-top: 20px;
        margin-bottom: 20px;
        padding: 7px;
        color: #ffffff;
        background-color: #eaeeef;
        display: table;
        text-align: center;
    }

    .blog-entry-readmore a
    {
        color: #777;
        font-size: 12px;
        font-weight: bold;
        text-decoration: none;
        text-transform: uppercase;
    }

    .views-field.views-field-addtoany-share {
        margin-bottom: 20px;
    }

    pre,
    pre *
    {
        word-break: break-word;
        white-space: pre-wrap;       /* Since CSS 2.1 */
        white-space: -moz-pre-wrap;  /* Mozilla, since 1999 */
        white-space: -pre-wrap;      /* Opera 4-6 */
        white-space: -o-pre-wrap;    /* Opera 7 */
        word-wrap: break-word;       /* Internet Explorer 5.5+ */
    }
}

@media screen and (max-width: 520px)
{
    .blog-container > .views-row,
    .blog-entry-container > .views-row
    {
        padding: 0;
        min-width: 260px;
        flex: 1;
    }

    #social
    {
        text-align: left;
        padding-top: 7px;
    }

    nav
    {
        width: 100%;
    }

    nav ul
    {
        padding-left: 0;
        flex-direction: column;
        display: flex;
    }

    nav ul li {
        padding: 8px 0 8px 0;
        border-top: 1px solid #999;
    }

    nav ul li:hover {
        background-color: #f5f5f5;
    }

    menu,
    menu div
    {
        margin-left:0;
        padding-left: 0;
        width: 100%;
    }

    #header-logo
    {
        width: 100%;
        max-width: none;
        padding-right: 20px;
    }

    #search
    {
        display: none;
    }

    #mobile-menu
    {
        display: inline;
        margin-left: auto;
        padding-top: 6px;
    }

    #mobile-menu a {
        color: #999;
        font-size: 20px;

    }

    #content
    {
        padding-right: 0;
        padding-bottom: 20px;
    }

    nav ul li a {
        margin-left: 5px;
    }

    nav ul li a.is-active {
        border: none;
    }

    nav ul li.menu-item--active-trail
    {
        background-color: #f5f5f5;
    }

    .spacer
    {
        height: 10px;
        background-color: #494a48;
        background-image: none;
        padding: 0;
        margin: 0;
        display: block;
    }

    .content-container > .views-row,
    .blog-entry-container > .views-row,
    .blog-container > .views-row
    {
        min-width: 300px;
        width: auto;
        max-width: 100%;
    }

    .blog-container * .views-field-body
    {
        padding-right: 0px;
    }

}
